export function upsetArr(arr){
    let _arr = arr.slice();
    for(let i = 0; i < _arr.length; i++){
        let j = getRandomIntFrom(0, i);
        let temp = _arr[i];
        _arr[i] = _arr[j];
        _arr[j] = temp;
    }
    return _arr
}

function getRandomIntFrom(min,max){
    return Math.floor(Math.random() * (max - min + 1) + min)
}

export function deepClone(obj){
    var toStr = Object.prototype.toString,
        arrType = '[object Array]',
        newObj = toStr.call(obj) === arrType ? [] : {};
    for(let key in obj){
        if(obj.hasOwnProperty(key)){
            if(typeof(obj[key]) === 'object' && obj[key] !== null){
                newObj[key] = deepClone(obj[key]);
            }else{
                newObj[key] = obj[key];
            }
        }
    }
    return newObj;      
}

/*
    
    handle
    delay
*/
export function debounce(handle, delay) {
    var timer;
    return function (...args) {
        if(timer){
            clearTimeout(timer);
        }
        timer = setTimeout(()=>{
            handle.apply(this, args);
        }, delay);
    }
}

/*
    
    handle:
    wait:
*/
export function throttle(handle, wait) {
    var lastTime = 0;
    return function () {
        //new Date().getTime()1970
        var nowTime = new Date().getTime();
        //
        if (nowTime - lastTime >= wait) {
            handle.apply(this, arguments);
            lastTime = nowTime;
        }
    }
}


